import {useState, useEffect} from 'react'
import firebase from './Base';

let AuthUser = () =>{
			// cree un state de base vide
    let [checkAuthUser, setCheckAuthUser] = useState(null)
		
		//au moment du chargement de la page
    useEffect(()=>{
				//quand la connection de l'utilisateur est changé 
        let unSubscribe = firebase.auth.onAuthStateChanged(user =>{
						//si user exit alors de déplace dans  le state 
            if(user){
                setCheckAuthUser(user)
						// sinon le met a null
            }else{
                setCheckAuthUser(null)
            }
        })
						//quand l'utilisateur quitte , le navigateur quitte bien
					return () => unSubscribe()
    }, [])
						//retourn le contenue de mon state 
    return checkAuthUser
}

export default AuthUser
