import React,{useState} from 'react'
import MessageListeAdmin from '../Base/imgListeAdmin';
import firebase from '../Base/Base';
import AuthUser from '../Base/AuthUser';
import GoogleButton from 'react-google-button'

function Admin() {
        let user = AuthUser()
        let [url , setUrl] = useState("")

        let handleCreateMessage = async () =>{
                    // cree le message que j'envoie 
            let newDessin = {
                    img: url,
                    time: Date.now()
                                }	
                    //ajoute "messages" dans la collection si il n'y est pas deja 
                    //et ajoute newMessage 
            await firebase.db.collection('dessin').add(newDessin)
        }

        let handleChange = (e) => {
            setUrl(e.target.value)
        }

        let handleSubmit = (e) => {
            e.preventDefault();
            if(url !== ""){
                handleCreateMessage()
                setUrl("")
            }else{
                return
            }
        }

        if(user && user.uid == "tDqLv12nM5MZCTuw3i5zqyj8x583"){
            return (
                <>
        
        <button onClick={() => firebase.logout()} className='logout'>
         Logout </button>
                    <h1 style={{textAlign:'center', paddingBottom:'50px'}}>Admin Panel</h1>
                    <div className="grilleAdmin">
                            <MessageListeAdmin/>
                    </div>
                    <div className='newItem'>
                        <form className='formAdmin' onSubmit={handleSubmit}>
                            <label className='labelAdmin' htmlFor="adminLabel">Add draw to gallery</label>
                            <input className='inputAdmin' value={url} onChange={handleChange} type="text" id='adminLabel' placeholder="Enter url"/>
                            <button className='buttonAdmin'  type='submit'>Comfirm</button>
                        </form>
                    </div>
                </>
            )
        }else{

        }
    return (
        <>
            {user ? (<button onClick={() => firebase.logout()} className='logout'>
    Logout </button>) : 
    (<GoogleButton className='btnGoogle' onClick={() => firebase.login("google")} />)}
           <h2 className='notAdmin'>Vous n'etes pas l'administrateur de ce site</h2>
        </>
    )


}

export default Admin
