import React, {useState, useEffect} from 'react'
import firebase from '../Base/Base';
import trash from '../Admin/delete.png'

//cree un composant qui vas lister les elements de ma database
function MessageListeAdmin() {
  let handleDelete = (item) => {
    let ref = firebase.db.collection("dessin").doc(item.id)
    ref.delete()
  }
    //cree un state 'message' , une liste vide
    let [monState, setMonState] = useState([])
    let handleSnapshot = (snapshot) =>{
				//cree un onject qui contient l'id du document qui est attribué de base 
				// et qui contient le contenue du doc avec "...doc.data()"
        let monState = snapshot.docs.map((doc) => ({id: doc.id , ...doc.data()}))				//ajoute messages a mon state 
        setMonState(monState)
    }
		
		//quand firebase est mis a jours
    useEffect(() => {
        let getMessage = () =>{

							/*vas chercher dans la database la collection message.
								le met dans l'ordre grave a mon Date.now() dans l'ordre décroissant
								et pour tout le snapshot c'est a dire tout le contentu de la collection
								message execute la fonction handleSnapshot.
							*/
             firebase.db.collection('dessin').orderBy("time", "desc").onSnapshot(handleSnapshot)
        }
					//retourn ma fonction getMessage
        return getMessage()

    }, [firebase])

    return (
        <>

       {monState.map((item, key) => (
         <>
       <img src={item.img} key={key} className="itemAdmin"/>
       <img src={trash} alt="poubelle" className='trash' onClick={() => handleDelete(item)} />
       </>
       ) )}

    </>
    )
}

export default MessageListeAdmin
